/* ==== */
/* Slide-in and Slide-out animation */
.page-transition {
  display: flex;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.slide-in {
  animation: slide-in 0.5s forwards;
}

@keyframes slide-in {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.slide-out {
  animation: slide-out 0.5s forwards;
}

@keyframes slide-out {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}

/* Fade-in and Fade-out animation */
.page-transition {
  display: flex;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}

.fade-in {
  animation: fade-in 1.5s forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-out {
  animation: fade-out 1.5s forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.toggle-custom-color {
  border-color: #65a500;
  background-color: #65a500;
}

.toggle-custom-color:checked::after {
  border-color: #65a500;
  background-color: #65a500;
}

.toggle-custom-color:checked {
  border-color: #65a500;
  background-color: #65a500;
}

.progress::-webkit-progress-value {
  background-color: var(
    --progress-color,
    #65a500
  ); /* Default to green if not provided */
}

.button-3d {
  border: 1px solid transparent;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); /* Adjust for 3D effect */
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

/* Mobile Button */

.mobile-button {
    filter: brightness(0) saturate(100%) invert(45%) sepia(85%) saturate(714%) hue-rotate(45deg) brightness(98%) contrast(103%);
}

/* Glass */
.glass {
    --glass-blur: 10px;
    --glass-opacity: 30%;
    --glass-border-opacity: 10%;
    --glass-reflex-degree: 1;
    --glass-reflex-opacity: 30%;
    --glass-text-shadow-opacity: 5%;
}

@media screen and (max-width: 768px) {
    .glass {
        --glass-blur: 40px;
    }
}